/* This file contains styles shared by both light & dark themes.
   Style rules can be customised using SCSS variables set in dark.scss & light.sccs.
   How to add a rule that uses different values for each theme.

   1. Add a variable in light.scss & dark.scss files, it needs to be set before
      "@import ./shared" statement.
      Example:

      $foo = '#000';
 
   2. Add a style rule in _shared.scss (this file) that uses new variable.
      Example:

      .my-class { background-color: $foo; }

   Instead of setting custom variables you can re-use any of bootstrap SCSS variables
   as they are all imported and ready.
   Resources:
   - https://getbootstrap.com/docs/4.6/getting-started/theming/
   - https://github.com/twbs/bootstrap/blob/v4.6.0/scss/_variables.scss
  */

  .alert-cell {
    color: $alert-cell-color;
    background: $rule-cell-bg;
  }
  
  .rule-cell {
    background-color: $rule-cell-bg;
  }
  
  .config-yaml {
    display: block;
    padding: 10px;
    font-size: 13px;
    color: $config-yaml-color;
    word-break: break-all;
    background-color: $config-yaml-bg;
    border: 1px solid $config-yaml-border;
    border-radius: 4px;
  }
  
  .query-stats {
    flex-grow: 1;
    font-size: 0.7rem;
    color: $query-stats-color;
  }
  
  .metrics-explorer.modal-dialog {
    max-width: 750px;
    overflow-wrap: break-word;
  }
  .metrics-explorer .metric {
    cursor: pointer;
    margin: 0;
    padding: 5px;
  }
  .metrics-explorer .metric:hover {
    background: $metrics-explorer-bg;
  }
  button.metrics-explorer-btn {
    color: $input-group-addon-color;
    background-color: $input-group-addon-bg;
    border: $input-border-width solid $input-group-addon-border-color;
  }
  
  .graph-controls button.clear-time-btn,
  .table-controls button.clear-time-btn {
    background-color: $input-bg;
    border: $input-border-width solid $input-border-color;
    border-left: none;
  
    &:hover {
      color: darken($secondary, 10%);
    }
  }
  
  button.execute-btn {
    width: 84px;
    border: $input-border-width solid darken($primary, 5%);
  }
  
  .expression-input .cm-expression-input {
    border: $input-border-width solid $input-border-color;
    flex: 1 1 auto;
    padding: 4px 0 0 8px;
    font-size: 15px;
  }
  
  .tab-content {
    border-left: 1px solid $nav-tabs-border-color;
    border-right: 1px solid $nav-tabs-border-color;
    border-bottom: 1px solid $nav-tabs-border-color;
    padding: 10px;
  }
  
  /* bootstrap-dark seems to break overflow on modals, making them unscrollable
     this fixes it by allowing overflow and enabling scrolling */
  .modal.show {
    overflow-y: auto;
  }
  
  .panel {
    margin-bottom: 20px;
  }

  input[type='checkbox']:checked + label {
    color: #286090;
  }

  .custom-control-label {
    cursor: pointer;
  }

  .togglers-wrapper .form-group {
    margin-bottom: 0.5rem;
  }

  [for$='-toggler'].custom-control-label::before,
  [for$='-toggler'].custom-control-label::after {
    top: 0.28rem;
    left: -1.3rem;
    width: 1.12rem;
    height: 1.12rem;
  }

  .capitalize-title::first-letter {
    text-transform: capitalize;
  }

  /* Using a more specific selector here to be able to override Bootstrap's default input group styles. */
  .input-group.expression-input {
    margin-bottom: 10px;
    /* Prevent the input group from wrapping around when the editor content is too long for a line. */
    flex-wrap: nowrap;
  }

  .alert.alert-danger {
    margin-bottom: 10px;
  }

  .nav-tabs .nav-link {
    cursor: pointer;
  }

  .tab-content .alert {
    margin-bottom: 0;
  }

  .data-table.table {
    margin: 10px 0 2px 0;
  }

  .data-table > tbody > tr > td {
    padding: 5px 0 5px 8px;
    font-size: 0.8em;
    overflow: hidden;
  }

  .autosuggest-dropdown {
    position: absolute;
    border: 1px solid #ced4da;
    background-color: #fff;
    color: #495057;
    font-size: 1rem;
    z-index: 1000;
    left: 56px;
    margin-top: -6px;
  }

  .autosuggest-dropdown-list {
    padding: 0;
    margin: 0;
    list-style: none;
  }

  .autosuggest-dropdown-list li {
    width: 100%;
    padding: 0.25rem 1.5rem;
    clear: both;
    white-space: nowrap;
    background-color: transparent;
    border: 0;
    display: block;
  }

  .autosuggest-dropdown-list li.autosuggest-dropdown-header {
    background-color: #bfdeff;
    font-size: 10px;
    line-height: 1.5;
    text-transform: uppercase;
    text-align: center;
  }

  .graph-controls,
  .table-controls {
    margin-bottom: 10px;
  }

  .graph-controls input,
  .table-controls input {
    text-align: center;
  }

  .graph-controls .range-input input {
    width: 50px;
  }

  .time-input input {
    border-right: none;
  }

  .time-input {
    width: 270px !important;
  }

  .graph-controls input.resolution-input {
    width: 90px;
  }

  .graph-controls > :not(:first-child) {
    margin-left: 20px;
  }

  .graph-legend {
    margin: 15px 0 15px 55px;
    font-size: 0.75em;
    padding: 10px 5px;
    display: inline-block;
  }

  .legend-item {
    cursor: pointer;
    display: flex;
    padding: 0 5px;
    border-radius: 3px;
    line-height: 1.7;
  }

  .legend-item div {
    flex-wrap: wrap;
  }

  .legend-swatch {
    min-width: 7px;
    height: 7px;
    outline-offset: 1px;
    outline: 1.5px solid #ccc;
    margin: 6px 8px 2px 0;
    display: inline-block;
  }

  .legend-item:hover {
    background: rgba(0, 0, 0, 0.18);
  }

  .legend-metric-name {
    margin-right: 1px;
  }

  .legend-label-name {
    font-weight: bold;
  }

  .graph {
    margin: 0 5px 0 5px;
  }

  .graph-chart {
    height: 500px;
    width: 100%;
    /* This is picked up by Flot's axis label font renderer,
        which ignores "color" and uses "fill" instead. */
    fill: #495057;
    font-size: 0.8em;
  }

  .graph-chart .flot-overlay {
    cursor: crosshair;
  }

  .graph-tooltip {
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 12px;
    white-space: nowrap;
    padding: 8px;
    border-radius: 3px;
  }

  .graph-tooltip .labels {
    font-size: 11px;
    line-height: 11px;
  }

  .graph-tooltip .detail-swatch {
    display: inline-block;
    width: 10px;
    height: 10px;
  }

  .add-panel-btn {
    margin-bottom: 20px;
  }

  .target-head {
    font-weight: 700;
    font-size: large;
  }

  .group-info {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    padding: 10px;
  }

  .badges-wrapper > span {
    margin-right: 5px;
    max-height: 20px;
  }

  .rules-head {
    font-weight: 600;
  }

  .rule_cell {
    white-space: pre-wrap;
    background-color: #f5f5f5;
    display: block;
    font-family: monospace;
  }

  .popup-message {
    display: none;
    position: absolute;
    top: 50%;
    right: 100%;
    transform: translateY(-50%);
    background-color: rgba(0, 0, 0, 0.7);
    color: #ffffff;
    padding: 8px 12px;
    border-radius: 4px;
    margin-right: 15px;
    min-width: 120px;
    max-width: 250px;
    width: max-content;
    white-space: normal;
    font-size: 14px;
}

.custom-control-label:hover .popup-message {
  display: block; 
}
